import React from 'react';
import './imageList.css'
class SearchBar extends React.Component {
    

    state = {
        term: ''
    }

    onFormSubmit = (event) => {
        event.preventDefault();

        this.props.onSubmit(this.state.term);
    }
    render() {
        return(
            <div className = "ui segment image-search">
                <form onSubmit={this.onFormSubmit} className = "ui form">
                    <div className = "field">
                        <label>image search</label>
                        
                        <input type="text"  className='search-input' value = {this.state.term} onChange={(e) => this.setState ( {term: e.target.value})}></input>
                        {/* another way of single line event handler */}
                        {/* <input type="text" onChange={(event) => console.log(event.target.value)}></input> */}
                    </div>
                    
                </form>
            </div>
        );
    }
};

export default SearchBar;